import React from 'react';
import HPFlatlay from '../img/hotelprices_flatlay.jpg';
import HPMacbook from '../img/hotelprices_macbook.jpg';
import HPMobile from '../img/hotelprices_mobile.jpg';
import HPScroll from '../img/hotelprices_scroll.mp4';
import HPHover from '../img/hotelprices_hover.mp4';
import HPCompare from '../img/hotelprices_compare.mp4';
import HPTransitions from '../img/hotelprices_transitions.mp4';
import HPInteractions from '../img/hotelprices_interactions.mp4';
import HPDesignGuide from '../img/hotelprices_styleguide.gif';










class HPProjectPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      scrolled: 0
    };
  }

  componentDidMount() {
    window.addEventListener("scroll", this.scrollProgress);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.scrollProgress);
  }

  scrollProgress = () => {
    const scrollPx = document.documentElement.scrollTop;
    const winHeightPx =
      document.documentElement.scrollHeight -
      document.documentElement.clientHeight;
    const scrolled = `${scrollPx / winHeightPx * 100}%`;

    this.setState({
      scrolled: scrolled
    });
  };

  render() {
    const progressContainerStyle = {
      background: "transparent",
      height: "2px",
      position: "relative",
      top: 0,
      left: 0,
      width: "100vw",
      zIndex: 99
    };

    const progressBarStyle = {
      height: "2px",
      background: "#000000",
      width: this.state.scrolled
    };

    return (
      <div>
      	<div className="project-title">
	        <div id="title">A Travel Data Tool for Hotels.com</div>
	        <div className="progress-container" style={progressContainerStyle}>
	          <div className="progress-bar" style={progressBarStyle} />
	        </div>
	    </div>
        <div id="project-page">
	    	<div className="project-text padding-top-2em">
		        <h1 className="padding-top-1em">How might we design a digital tool to explore hotel prices around the world?</h1>
		    </div>
		    <div className="project-text padding-top-2em">
		        <div className="project-caption-half padding-top-1em">
			        <h3>Context</h3>
			        <h5 className="padding-top-1em">An interactive visualisation tool to explore Hotels.com travel data, designed in 2017 at Battenhall.</h5>
		        </div
		        ><div className="project-caption-half padding-top-1em">
			        <h3>Role</h3>
			        <h5 className="padding-top-1em">UX/UI and interaction design. Design team: Rebecca Gill Clarke, Aaron Griffiths, Andrea Esteban Martn.</h5>
		        </div>
		    </div>
	        <img className="padding-top-3em" src={HPFlatlay} alt="..." />		    
		    <div className="project-text padding-top-3em">
		        <h2>The purpose</h2>
		        <p className="padding-top-1em">Hotel Prices Around the World is an interactive application which can help you to make the most of your travel budget and provide inspiration for your next trip.</p>
		        <p className="padding-top-1em">It was developed as the result of a brief from Hotels.com, who wanted to create a graphic tool with a clear call to action that could be easily shared on social media to drive sales. Data was pulled from their Hotel Price Index site which houses information about hotel prices from over 30 popular destinations.</p>
	        </div>
	        <img className="padding-top-3em" src={HPMacbook} alt="The default desktop view" />
	        <div className="project-text padding-top-3em">
		        <h2>Simplifying data</h2>
		        <p className="padding-top-1em">Our challenge was to find a creative way of showcasing all of this information in a way that would engage rather than overwhelm the user. The product needed to be functional across all devices and able to be easily updated by the Hotels.com team.</p>
		        <p className="padding-top-1em">It also needed to be built and adapted for thirteen different countries, taking into account the varying data reports, language translations and currency exchange of each country.</p>
	        </div>
	        <img className="padding-top-3em" src={HPMobile} alt="Various mobile screens" />
	        <div className="project-text-half padding-top-3em">
		        <h2>User interface</h2>
		        <p className="padding-top-1em">On page load, users are presented with a colour-coded key which they can use to filter cities by regions such as Asia, Europe and North America. A comparison toolbar allows users to weigh up the price differences between up to four cities. Cities are added to the toolbar by clicking the plus icon.</p>
		        <p className="padding-top-1em">Next to the plus icon is an expand symbol which activates each citys individual data card. The city squares are organised by average hotel price, with the most expensive city being the first and largest square.</p>
	        </div>
		    <video className="project-text-half padding-top-3em" autoPlay loop muted playsinline>
    			<source src={HPScroll} alt="Scrolling through the default desktop view" type='video/mp4' />
			</video>	
	        <div className="project-text-half padding-top-3em">
		        <h2>Double-sided cards</h2>
		        <p className="padding-top-1em">The expanded city data cards are actually double-sided, with city info on one side and country info on the other. The city side features useful stats such as average hotel price, average prices by star rating, and annual percentage rises/falls.</p>
		        <p className="padding-top-1em">The country side highlights hotel prices and percentage rises/falls of the overall country, but the desktop version also incorporates a map which users can hover over to explore other cities in that country. On mobile, the map is simplified to a dropdown menu.</p>
	        </div>
		    <video className="project-text-half padding-top-3em" autoPlay loop muted playsinline>
    			<source src={HPHover} alt="Hovering over US cities in a country data card" type='video/mp4' />
			</video>	
	        <div className="project-text-half padding-top-3em">
		        <h2>Comparing cities</h2>
		        <p className="padding-top-1em">The comparison bar allows users to view their chosen city cards in a scrollable list format. Individual cards can be removed from the list by clicking the X icon. Additionally, users can flip to the country side of their cards whilst still in comparison mode, and use the map to continue exploring other cities.</p>
		        <p className="padding-top-1em">To start over, the user can delete cards one-by-one or click the trash icon to return to the bars original state. The bar is preloaded with empty segments so the user knows how many cities they can compare.</p>
	        </div>
		    <video className="project-text-half padding-top-3em" autoPlay loop muted playsinline>
    			<source src={HPCompare} alt="Comparing hotel prices in New York, Amsterdam and Mexico City" type='video/mp4' />
			</video>	
	        <div className="project-text padding-top-3em">
		        <h2>Swiping and flipping</h2>
		        <p className="padding-top-1em">The data cards feature some interesting UI transitions; on desktop, the city side of the card swipes to the left and the country card slides in from the right, whilst on mobile the card flips over to reveal the country data on the other side.</p>
		        <p className="padding-top-1em">In comparison mode on mobile, we integrated a swiping transition which allows users to navigate left and right through their selection of cities, whilst still being able to flip cards over to view the country side if needed.</p>
	        </div>
		    <video className="padding-top-3em" autoPlay loop muted playsinline>
    			<source src={HPTransitions} alt="Transitioning through city and country cards" type='video/mp4' />
			</video>	
	        <div className="project-text padding-top-3em">
		        <h2>User interactions</h2>
		        <p className="padding-top-1em">The comparison bar also uses sliding and fading interactions when adding cities, comparing and clearing the bar. These subtle animations help to enhance basic interactions and makes the user experience feel smoother.</p>
		        <p className="padding-top-1em">Classic bar charts were used to represent hotel price stats as well as more unusual visualisations, such as the circular graphic used for the average hotel price by star rating data.</p>
	        </div>
		    <video className="padding-top-3em" autoPlay loop muted playsinline>
    			<source src={HPInteractions} alt="Mobile screen recording of the comparison bar interaction" type='video/mp4' />
			</video>	

	        <div className="project-text-half padding-top-3em">
		        <h2>Branded assets</h2>
		        <p className="padding-top-1em">We selected five hues from the Hotels.com brand palette to create the regional colour-coded key. Each regions colour is used boldly across the interactive, from the individual city squares to the data cards, in order to create an easily understood visual system.</p>
		        <p className="padding-top-1em">The city squares use a diagonal gradient to allow cities from the same region to sit next to each other without blending into one block. We adhered to brand guidelines by using Roboto as our chosen typeface, but we designed a unique icon set and vector maps for each city.</p>
	        </div>
	        <img className="project-text-half padding-top-3em" src={HPDesignGuide} alt="Showcasing thecolour-coded design system" />
	        <br />
	        <br />
	        <br />
      	</div>
      </div>
    )
  }
}


export default HPProjectPage;







